import React, { useState, useRef } from 'react';

import { useDispatch } from 'react-redux';
import { addItem } from '../store/cartStore';

import Button from 'react-bootstrap/Button';
import Overlay from 'react-bootstrap/Overlay';
import Popover from 'react-bootstrap/Popover';
import Kakao from './Maps';

function TourSelfList({ a, i }) {
  // let [like, setLike] = useState(false);
  // ===============================================================================================

  const randomInt = Math.floor(Math.random() * 10000);
  // 0  10   
  // ===============================================================================================
  let [storage, setStorage] = useState([]);
  const saveDataToLocalStorage = (
    title,
    addr,
    tel,
    mapLat,
    mapLot,
    summ,
    dtlAddr
  ) => {
    const data = { title, addr, tel, mapLat, mapLot, summ, dtlAddr };
    localStorage.setItem('selected', JSON.stringify(data));
    setStorage(data);
  };
  // ===============================================================================================
  const [show, setShow] = useState(false);
  const [target, setTarget] = useState(null);
  const ref = useRef(null);

  const handleClick = (event) => {
    setShow(!show);
    setTarget(event.target);
  };
  // ===============================================================================================

  let dispatch = useDispatch();

  return (
    <li
      className="selfList_li"
      onClick={() => {
        saveDataToLocalStorage(
          a.tourspotNm,
          a.tourspotAddr,
          a.refadNo,
          a.mapLat,
          a.mapLot,
          a.restrntSumm,
          a.tourspotDtlAddr
        );
      }}
    >
      <div className="selfList_list" ref={ref}>
        <div className="selfList_imgCon">
          <img src={`${process.env.PUBLIC_URL}/image/like.png`} alt="" />
          <p>{randomInt}</p>
        </div>
        <p className="selfList_list_title">{a.tourspotNm}</p>
        <p className="selfList_list_Addr">{a.tourspotAddr}</p>
        <div className="btn_con">
          <Button onClick={handleClick}>  + </Button>
        </div>

        <Overlay
          show={show}
          target={target}
          placement="top"
          container={ref}
          containerPadding={20}
        >
          <Popover id="popover-contained">
            <Popover.Header as="h3">{a.tourspotNm}</Popover.Header>
            <Popover.Body>
              <div className="toolTips_con">
                <div className="toolTips_map">
                  <Kakao item={storage} />
                </div>
                <div className="toolTips_pcon">
                  <p className="toolTips_title">
                    {storage.title ? storage.title : ' '}
                  </p>
                  <p className="toolTips_addr">
                    <strong> </strong>
                    {storage.addr ? storage.addr : ' .'}
                  </p>
                  <p className="toolTips_dtlAddr">
                    <strong> </strong>
                    {storage.dtlAddr ? storage.dtlAddr : ' '}
                  </p>
                  <p className="toolTips_tel">
                    <strong> </strong>
                    {storage.tel ? storage.tel : ' '}
                  </p>
                  <p className="toolTips_summ">
                    <strong></strong>
                    <span>
                      {storage.summ ? storage.summ : ' '}
                    </span>
                  </p>
                </div>
                <div className="toolTips_Pcon">
                  <button
                    onClick={() => {
                      dispatch(
                        addItem({
                          id: i,
                          title: a.tourspotNm,
                          addr: a.tourspotAddr,
                          tel: a.refadNo,
                          mapLat: 'a.mapLat',
                          mapLot: 'a.mapLot',
                          summ: a.restrntSumm,
                          dtlAddr: a.tourspotDtlAddr,
                        })
                      );
                    }}
                  >
                    
                  </button>
                </div>
              </div>
            </Popover.Body>
          </Popover>
        </Overlay>
      </div>
    </li>
  );
}

export default TourSelfList;
