import Cart from './Cart';

function Select({ handleSearchGu, handleSearchDong, handleSelect, chooseGu }) {
  return (
    <section className="Select mw">
      <div className="selectCon">
        <select onChange={handleSearchGu} name="name1" id="">
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
        </select>
        <select onChange={handleSearchDong} name="name2" id="">
          <option value=""></option>
          {chooseGu.map((key, i) => (
            <option key={i} value={key}>
              {key}
            </option>
          ))}
        </select>
        <select onChange={handleSelect} name="name3" id="">
          <option value="1"></option>
          <option value="2"></option>
          <option value="3"></option>
          <option value="4"></option>
        </select>
      </div>
      <Cart />
    </section>
  );
}

export default Select;
